* {
  z-index: 2;
}
.AUTHENTICATION {
  overflow: hidden;
}
#user-id {
  position: absolute;
  width: 5.5vw;
  height: 3.3vh;
  left: 56vw;
  top: 37vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  background-color: white;
  background: none;
}

#password {
  position: absolute;
  width: 15.5vw;
  height: 3.3vh;
  left: 56vw;
  top: 52vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  background-color: white;
  background: none;
}

#input-box1 {
  box-sizing: border-box;
  font-family: "Montserrat";
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 40vh;
  font-weight: 1000;
  font-size: 1.1rem;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  background-color: white;
  padding-left: 7vh;
}

#input-box2 {
  box-sizing: border-box;
  font-family: "Montserrat";
  position: absolute;
  width: 26vw;
  height: 6.5vh;
  left: 56vw;
  top: 55vh;
  font-weight: 1000;
  font-size: 1.1rem;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  background-color: white;
  padding-left: 7vh;
}

::placeholder {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 17px;
  color: #afafaf;
}

#wrongid {
  position: absolute;
  height: 5vh;
  left: 56vw;
  top: 47.5vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  background-color: white;
  color: #ff0000;
  display: none;
  background: none;
}

#wrongpass {
  position: absolute;
  width: 26vw;
  height: 20vh;
  left: 56vw;
  top: 62.5vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  background-color: white;
  color: #ff0000;
  display: none;
  background: none;
}

#btn-submit {
  box-sizing: border-box;
  font-size: 1.1rem;
  position: absolute;
  width: 26vw;
  height: 7vh;
  left: 56vw;
  top: 76vh;
  letter-spacing: 1px;
  background: #6c63ff;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
}

#btn-submit:hover {
  background: #7971ff;
}

#btn-submit:active {
  transform: scale(0.99);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

#heading {
  position: absolute;
  width: 35vw;
  height: 17vh;
  left: 53vw;
  top: 19vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  font-size: 2.125em;
  line-height: 44px;
  /* identical to box height */
  background: none;
  color: #000000;
}

#emailImage {
  background: none;
  width: 25px;
  height: 19px;
  position: absolute;
  top: 41.8vh;
  left: 56.7vw;
  z-index: 5;
}

#lockImg {
  background: none;
  width: 30px;
  height: 21px;
  position: absolute;
  top: 56.4vh;
  left: 56.5vw;
  z-index: 5;
}

#fgtPwd {
  position: absolute;
  top: 73vh;
  left: 71.4vw;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 15.5px;
  line-height: 17px;
  color: #000000;
  background: none;
}

#credential {
  position: absolute;
  width: 22vw;
  /* height: 20vh; */
  left: 59vw;
  top: 84vh;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 17px;
  color: #ff0000;
  background: none;
}

#eye {
  position: absolute;
  top: 57.4vh;
  left: 79.5vw;
  background: none;
  cursor: pointer;
}
